<!-- 搜索结果 -->
<template>
  <div class="search-result">
    <div class="fixed">
      <SearchBar :initVal="initVal" @searchVal="searchVal" ></SearchBar>
      <div class="tab">
        <div 
          @click="changeTab(index)"
          :class="['item', activeIndex === index ? 'active' : '']" 
          v-for="(item, index) in ['视图', '文章']" 
          :key="index">
          {{item}}
          <i></i>
        </div>
      </div>
    </div>
    <div class="content">
      <div v-show="activeIndex === 0">
        <div 
          class="result-app-item" 
          v-for="item in viewList" 
          @click="toDetail(item.url, item.modulename)"
          :key="item.moduleid">
          <img :src="item.image" alt="">
          <div class="result-content">
            <h4>{{item.modulename}}</h4>
            <p>{{item.description}}</p>
            <span>{{item.fangwenTel}}</span>
          </div>
        </div>
        <img class="loading" v-if="isLoadingShow" src="../../assets/images/loading.gif" alt="">
        <h6 class="empty" v-if="viewList.length === 0 && !isLoadingShow">空空如也</h6>
      </div>
      <div v-show="activeIndex === 1">
        <div 
          class="result-article-item" 
          v-for="(item, index) in articleList" 
          :key="item.activityid">
          <h3 @click="toDetail(item.textpath, item.textname)">{{item.textname}}</h3>
          <h5 @click="toDetail(item.textpath, item.textname)">{{item.authorname}} {{item.createTime}}</h5>
          <div class="text-img" @click="toDetail(item.textpath, item.textname)">
            <p>{{item.brief}}</p>
            <img :src="item.imagename" alt="">
          </div>
          <div class="handle">
            <span :class="['zan', item.dianzan ? 'zanActive' : '']" @click="zanHandle(item.activityid, index)">{{item.dianzanliang ? item.dianzanliang : '点赞'}}</span>
            <span :class="['collection', item.shoucang ? 'collectionActive' : '']" @click="collectionHandle(item.activityid, index)">{{item.shoucangliang ? item.shoucangliang : '收藏'}}</span>
          </div>
        </div>
        <img class="loading" v-if="isLoadingShow" src="../../assets/images/loading.gif" alt="">
        <h6 class="empty" v-if="articleList.length === 0 && !isLoadingShow">空空如也</h6>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import moment from 'moment';
import SearchBar from "../../components/SearchBar";
export default {
  name: "SearchResult",
  components: {
    SearchBar
  },
  data() {
    return {
      initVal: this.$route.query.value,
      activeIndex: 0,
      inputVal: '',
      viewList: [],
      articleList: [],
      isLoadingShow: false
    };
  },
  created() {},
  mounted() {
    this.searchVal(this.initVal);
    this.activeIndex = this.$route.query.isArticle ? 1 : 0;
  },
  methods: {
    // 获取搜索框值
    searchVal(val) {
      this.inputVal = val;
      if (this.inputVal) {
        this.isLoadingShow = true;
        this.isEmptyShow = false;
        this.viewList = [];
        this.articleList = [];
        this.$axios.post('/hopeModule/searchModuleByNameSec', {
          "name": this.inputVal,
          "aamid": this.$store.state.userInfoObj.aamid,
          "deptid": this.$store.state.userInfoObj.deptid,
					"odeptid": this.$store.state.userInfoObj.odeptid
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
        }).then(res => {
        	this.isLoadingShow = false;
        	if (res.status === 200 && res.data.status === '000000') {
            const resData = res.data.data || [];
            const viewKeyList = resData.module.list[1].styleList || [];
            const viewTableList = resData.module.list[0].styleList || [];
            const articleKeyList = resData.article.list[1].styleList || [];
            const articleTableList = resData.article.list[0].styleList || [];
            this.viewList = viewKeyList.concat(viewTableList);
            this.articleList = articleKeyList.concat(articleTableList);
            this.articleList.forEach(item => {
              item.createTime = item.timeori ? moment(item.timeori).format("YYYY/MM/DD HH:mm:ss") : '';
            });
        	} else {
        		this.isEmptyShow = true;
        	}
        }).catch(err => {
        	this.isLoadingShow = false;
        	this.isEmptyShow = true;
        });
      }
    },
    // 点赞
    zanHandle(activityid, index) {
      const dianzanliang = this.articleList[index].dianzanliang;
      Toast.loading({
        message: '请稍后...',
        forbidClick: true
      });
      this.$axios.post('/hopeActivity/commendhopeActivity', {
        "activityid": activityid,
        "aamid": this.$store.state.userInfoObj.aamid,
        // "aamid": "010203",
        "viewtime": new Date(),
        "liked": 1
      }).then(res => {
        Toast.clear();
        if (res.status === 200 && res.data.status === '000000') {
          this.articleList[index].dianzan = true;
          this.articleList[index].dianzanliang = dianzanliang + 1;
        } else {
          Toast.fail('请重试');
        }
      }).catch(err => {
        Toast.clear();
        Toast.fail('请重试');
      });
    },
    // 收藏
    collectionHandle(activityid, index) {
      const isCollection = this.articleList[index].shoucang;
      const shoucangliang = this.articleList[index].shoucangliang;
      const url = isCollection ? 'deleteCollActivi' : 'colletcthopeActivity';
      const opt = isCollection ? 
        { 
          "aamid": this.$store.state.userInfoObj.aamid,
          // "aamid": "010203",
          "activityid": activityid,
        } : 
        {
          "activityid": activityid,
          "aamid": this.$store.state.userInfoObj.aamid,
          // "aamid": "010203",
          "activitysequence": new Date(),
          "favortype": 1
        }
      Toast.loading({
        message: '请稍后...',
        forbidClick: true
      });
      this.$axios.post(`/hopeActivity/${url}`, opt).then(res => {
        Toast.clear();
        if (res.status === 200 && res.data.status === '000000') {
          this.articleList[index].shoucang = !isCollection;
          let newShoucangliang;
          if (isCollection) {
            newShoucangliang = shoucangliang - 1;
          } else {
            newShoucangliang = shoucangliang + 1;
          }
          this.articleList[index].shoucangliang = newShoucangliang;
        } else {
          Toast.fail('请重试');
        }
      }).catch(err => {
        Toast.clear();
        Toast.fail('请重试');
      });
    },
    // 收集搜索历史
    gatherHistory(text) {
      return new Promise((resolve, reject) => {
        this.$axios.post(`/hopeSearchHistory/insertSearchRecord`, {
          "aamid": this.$store.state.userInfoObj.aamid,
          // "aamid": "010203",
          "searchtext": text,
          "logtime": new Date()
        }).then(res => {
          if (res.status === 200 && res.data.status === '000000') {
            resolve();
          } else {
            reject();
          }
        }).catch(err => {
          reject();
        });
      });
    },
    // 跳转详情
    toDetail(url, title) {
      this.gatherHistory(title).then(() => {
        window.location.href = url;
      }).catch(() => {
        window.location.href = url;
      })
    },
    changeTab(index) {
      this.activeIndex = index;
    }
  }
};
</script>
<style lang='less' scoped>
/* @import url(); 引入css类 */
  .search-result{
    background: #f1f1f1;
    overflow: hidden;
  }
  .fixed{
    position: fixed;
    width: 100%;
    top: 0;
  }
  .tab{
    height: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #fff;
    margin-top: -1px;
    .item{
      width: 0.62rem;
      text-align: center;
      font-size: 0.15rem;
      position: relative;
      height: 100%;
      line-height: 0.5rem;
      i{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 4px;
        background: #45A4B1;
        border-radius: 2px;
        display: none;
      }
    }
    .active{
      font-size: 0.16rem;
      font-weight: 500;
      i{
        display: block;
      }
    }
  }
  .content{
    padding-top: 1rem;
    .empty{
      text-align: center;
      line-height: 0.6rem;
      font-size: 0.14rem;
      color: #969696;
    }
    .loading{
      display: block;
      width: 0.4rem;
      margin: 0.2rem auto;
    }
    .result-app-item{
      width: 3.55rem;
      height: 1.15rem;
      box-sizing: border-box;
      padding: 0.1rem;
      background: #fff;
      margin: 0.1rem;
      border-radius: 4px;
      display: flex;
      img{
        width: 0.95rem;
        height: 0.95rem;
        border-radius: 4px;
      }
      .result-content{
        padding-left: 0.1rem;
        overflow: hidden;
        h4{
          font-size: 0.14rem;
          color: #3A3A3A;
          line-height: 0.2rem;
          font-weight: 500;
          margin-top: 0.1rem;
        }
        p{
          font-size: 0.12rem;
          color: #969696;
          line-height: 0.16rem;
          height: 0.32rem;
          margin: 0.08rem 0;
          overflow : hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        span{
          display: inline-block;
          height: 0.18rem;
          line-height: 0.18rem;
          padding-left: 0.2rem;
          color: #A5A5A5;
          background: url('../../assets/images/icon_visits@2x.png') no-repeat left center;
          background-size: 0.18rem 0.18rem;
        }
      }
    }
    .result-article-item{
      width: 3.55rem;
      height: 1.85rem;
      box-sizing: border-box;
      padding: 0.14rem 0.1rem;
      background: #fff;
      margin: 0.1rem;
      border-radius: 4px;
      h3{
        font-size: 0.18rem;
        color: #232323;
        line-height: 0.25rem;
        margin-bottom: 0.04rem;
      }
      h5{
        font-size: 0.12rem;
        color: #969696;
        line-height: 0.16rem;
        height: 0.16rem;
        margin-bottom: 0.12rem;
      }
      .text-img{
        height: 0.66rem;
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.15rem;
        p{
          width: 2.3rem;
          font-size: 0.15rem;
          color: #3A3A3A;
          line-height: 0.22rem;
          overflow : hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
        img{
          width: 0.95rem;
          height: 0.64rem;
          border-radius: 4px;
        }
      }
      .handle{
        height: 0.2rem;
        line-height: 0.22rem;
        font-size: 0.12rem;
        color: #A5A5A5;
        letter-spacing: 0;
        .zan, .collection{
          float: left;
          height: 0.2rem;
          padding-left: 0.25rem;
          background: url('../../assets/images/btn_good_normal@2x1.png') no-repeat left center;
          background-size: 0.2rem 0.2rem;
          margin-right: 0.25rem;
        }
        .collection{
          background: url('../../assets/images/essay_btn_collect_gray@2x.png') no-repeat left center;
          background-size: 0.2rem 0.2rem;
        }
        .zanActive{
          background: url('../../assets/images/btn_good_selected@2x.png') no-repeat left center;
          background-size: 0.2rem 0.2rem;
        }
        .collectionActive{
          background: url('../../assets/images/btn_collect_selected@2x.png') no-repeat left center;
          background-size: 0.2rem 0.2rem;
        }
      }
    }
  }
</style>